export default {
  name: "MyButton",
  props: {
    type: {
      type: String,
      default: "button",
      validator: value => {
        return ['text', 'button'].includes(value);
      }
    },
    hover: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    handleClick() {
      this.$emit("click");
    }
  }
};