export default {
  name: 'MyTooltip',
  props: {
    lines: {
      type: Number,
      default: 1
    },
    alwaysShow: {
      type: Boolean,
      default: false
    },
    content: {
      type: String,
      default: ''
    },
    placement: {
      type: String,
      default: 'top'
    }
  },
  data() {
    return {
      isOverflow: false,
      internalContent: ''
    };
  },
  methods: {
    checkOverflow() {
      const textEl = this.$refs.textRef;
      this.internalContent = textEl.innerText;
      this.isOverflow = textEl.scrollHeight > textEl.clientHeight;
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.checkOverflow();
    });
  }
};