import "core-js/modules/es.iterator.filter.js";
import "core-js/modules/es.iterator.find.js";
import MyButton from '../Button';
import propsMixin from '@/mixins/props';
import modelMixin from '@/mixins/model';
let dot = null;
export default {
  name: "MyDropdown",
  mixins: [propsMixin, modelMixin],
  props: {
    options: {
      type: Array,
      default: () => []
    },
    trigger: {
      type: String,
      default: "click",
      validator: value => {
        // 只允许 'click', 'hover', 'contextmenu' 三种值
        return ['click', 'hover', 'contextmenu'].includes(value);
      }
    },
    placement: {
      type: String,
      default: 'bottom-start'
    }
  },
  components: {
    MyButton
  },
  computed: {
    optionsFilter() {
      return this.options.filter(option => option.visible !== false);
    },
    showLabel() {
      if (!this.internalValue) return '';
      const findOption = this.optionsFilter.find(option => option.value === this.internalValue);
      if (findOption) {
        return findOption.showLabel || findOption[this.defaultProps.value];
      }
      return '';
    },
    internalTrigger() {
      if (this.trigger === 'contextmenu') {
        return null;
      } else {
        return this.trigger;
      }
    }
  },
  data() {
    return {
      showPopper: false
    };
  },
  methods: {
    handleCommand(option) {
      this.$emit('input', option.value); // 实现 v-model
      // 扩展了option参数，方便获取内容
      this.$emit('command', option.value /*command*/, option);
    },
    handleContextMenu(event) {
      if (this.trigger !== 'contextmenu') return;
      event.preventDefault();
      if (!dot) {
        dot = document.createElement('div');
        dot.style.position = 'absolute';
        document.body.append(dot);
      }
      dot.style.left = `${event.pageX}px`;
      dot.style.top = `${event.pageY}px`;
